<template>
    <div class="setting-title">
        <h1 class="component-name mb20">小程序顶部 {{ $attrs.currentIndex }}</h1>
        <div class="title-component">
            <div>
                <div class="mb20">
                    <p class="pb10">背景图片：</p>
                    <!-- TODO: 头部背景图片上传 -->
                    <el-upload
                        class="avatar-uploader ml50"
                        action="#"
                        :show-file-list="false"
                        :before-upload="beforeAvatarUpload"
                        :on-change="setBackgroundImg"
                    >
                        <!-- :on-success="handleAvatarSuccess" -->
                        <img v-if=" background_img" :src="background_img" class="avatar" />
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </div>

                <div class="mb20">
                    <p class="pb10">标题栏左侧图片：</p>
                    <!-- TODO: 标题栏左侧图片上传 -->
                    <el-upload
                        class="avatar-uploader ml50"
                        action="#"
                        :show-file-list="false"
                        :before-upload="beforeAvatarUpload"
                        :on-change="file => images.url = handleChange(file)"
                    >
                        <!-- :on-success="handleAvatarSuccess" -->
                        <img v-if="images.url" :src="images.url" class="avatar" />
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </div>

                <div>
                    <p class="pb10">标题栏图片跳转位置：</p>
                    <el-select
                        v-model="images.path"
                        placeholder="请选择需要跳转的页面"
                        clearable
                        class="ml50"
                    >
                        <el-option
                            v-for="selectItem in options"
                            :key="selectItem.value"
                            :label="selectItem.label"
                            :value="selectItem.value"
                        ></el-option>
                    </el-select>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            options: [
                {
                    value: "黄金糕",
                    label: "黄金糕"
                },
                {
                    value: "双皮奶",
                    label: "双皮奶"
                },
                {
                    value: "蚵仔煎",
                    label: "蚵仔煎"
                },
                {
                    value: "龙须面",
                    label: "龙须面"
                },
                {
                    value: "北京烤鸭",
                    label: "北京烤鸭"
                }
            ]
        };
    },
    props: {
        background_img: String,
        images: Object
    },
    methods: {
        /**
         *  背景图片
         */
        setBackgroundImg(file) {
            const url = URL.createObjectURL(file.raw);
            this.$emit("update:background_img", url);
        },
        /**
         *  TODO: 上传校验
         */
        beforeAvatarUpload(file) {
            const arr = [
                "image/jpeg",
                "image/jpg",
                "image/png",
                "image/gif",
                "image/webp"
            ];
            const isImg = arr.includes(file.type);

            if (!isImg) {
                this.$message.error("上传文件的格式不对！");
            }
            return isImg;
            // const isLt2M = file.size / 1024 / 1024 < 2;
        },

        /* TODO: 图片上传临时地址 */
        handleChange(file) {
            return URL.createObjectURL(file.raw);
        }
    }
};
</script>

<style scoped>
.component-name {
    border-bottom: 1px dashed #dfe6e9;
    padding-bottom: 10px;
}

.setWidth {
    width: 130px;
}

/* TAG: 右侧可滚动区域高度 */
.title-component {
    height: calc(100vh - 220px);
    overflow: auto;
}

/* ELEMENT_UI STYLE */
.setting-title /deep/ .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    height: 120px;
    display: inline-flex;
    align-items: center;
}
.setting-title /deep/ .avatar-uploader .el-upload:hover {
    border-color: #409eff;
}
.setting-title /deep/ .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 120px;
    height: 120px;
    line-height: 120px;
    text-align: center;
}
.setting-title /deep/ .avatar {
    width: 120px;
    display: block;
}
</style>



